.loginScreen {
    position: relative;
    height: 100%;
    background: url("https://genotipia.com/mi_content/uploads/2020/04/Netflix-Background-prueba-1.jpg")
    center no-repeat;
    background-size:cover;
}

.loginScreen__button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

.loginScreen__body > h1 {
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginScreen__body > h2 {
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen__body  h3 {
    font-size: 1.3rem;
    font-weight: 400;
}

.loginScreen__body {
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
    left: 0;
    right: 0;
    /* con left y right ayudo indirectamente a poner centrado el sigun o form */
}

.loginScreen_gradient {
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    /* filtro para lucir opaco  o negro */
    background-image: linear-gradient(
        to top,
        rgba(0, 0, 0, 0.8) 0,/* oscurese abajo */
        rgba(0, 0, 0, 0) 60%,/* oscurece centro */
        rgba(0, 0, 0, 0.8) 100%/* oscurece arriba */
    );
}

.loginScreen__logo {
    position: fixed;
    top: 20px;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen__input > form{
    /* todo sea para centrar el input y el btn */
    justify-content: center;
    display: flex;
    flex-direction: row;
}

.loginScreen__input > form > input{
    padding: 10px;
    outline-width: 0;/* al hacer focus en el input no me des bordes */
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.loginScreen__input {
    margin: 20px;
}

.loginScreen__getStarted {
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}
